body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #76babd;
  
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.container{
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.container img{
  max-width: 40%;
}


.main{
  background-color: #f7e4c3;
  border-radius: 10px;
  border: 5px solid rgb(96, 252, 252);
  padding: 0.5rem;
  text-align: center;
  margin-top: 2rem;
  width: 50%;
  color: #000000;
}

@media only screen and (min-width: 768px) {
  
  .container img{
    max-width: 30%;
  }

  .main{
    width: 30%;
    margin-top: 0;
  }
}


.boton-buscar{
  background-color: #a2ffc1;
  border-radius: 15px;
  padding: 10px 25px;
  border-color: aqua;
  cursor: pointer;
  text-transform: uppercase;
  font-family: 'Itim', cursive;
  font-size: 1.2em;
  margin: 1rem 0;
  transition: all 0.4s ease-in-out;
  outline: none;
}

.boton-buscar:hover{
  background-color: #7effa9;
  border-color: #c7c6c6;
  transform: scale(1.1);
}

.boton-buscar:active{
  border: none!important;
  transform: scale(0.95);
  text-decoration: none;
  background-color: aqua;

}
.personaje{
  margin: 0 auto;
  text-align: center;
  padding: 0;
}

.personaje img{
  max-width: 85%;
  border-radius: 5px;
  border: 5px solid #b1f9fc;
}

.footer{
  background-color: #7effa9;
  border-radius: 7px 7px 0  0;
  width: 50%;
  text-align: center;
  position: absolute;
  top: 97vh;
}

.footer a{
  cursor: pointer;
  text-decoration: none;
  color: black;
  transition: all 0.2s ease-in-out;
  border-radius: 7px 7px 0 0;
}

.footer a:hover{
  background-color: #5af8e3;
  font-size: 1.1em;
}